@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #000000;
  box-sizing: border-box;
}
body{
  display: flex;
}
#root{
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000000;
}
.App{
  min-height: 100vh;
  max-width: 800px;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: white;
}

/* NavBar */
.navbar {
  position: sticky;
  top: 0%;
  padding: 20px 15px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #f2f2f2;
  z-index: 10000;
  transition: all 0.5s ease-in-out;
  background-color: white;
}
.navbar.active{
  padding: 10px 15px;
  background-color: rgb(0, 0, 0);
}
.navbar.active h1{
  color: rgba(255, 255, 255);
}
.navbar h1 {
  color: #583407;
  font-size: 30px;
  text-transform: uppercase;
}
.navbar .links {
  margin-left: auto; 
  display: flex;
}
.navbar .new-blog-link{
  background-color: #000000;
  color: white;
  border-radius: 10px;
  font-size: large;
  text-wrap: nowrap;
  margin: 10px;
  margin-left: 40px;
}
.navbar.active .new-blog-link{
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}
.navbar .new-blog-link:hover{
  background-color: white;
  color: rgb(0, 0, 0);
}
.navbar.active .new-blog-link:hover{
  background-color: rgb(236, 155, 149);
  color: rgb(0, 0, 0);
}
.navbar a{
  text-decoration: none;
  padding: 6px;
  transition: ease-in-out 0.5s;
}
.navbar .home{
  /* font-weight: 600; */
  float: left;
  width: 60px;
}
.navbar.active a{
  color: rgb(255, 255, 255);
}
.navbar a:hover {
  color: #720f08;
}

/* Blog Lists */
.content {
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
  margin-top: 30px;
  padding: 20px;
  background-color: white;
}
.blog-preview{
  padding: 10px 15px;
  margin: 5px 0px;
  border-bottom: 1px solid #e8e8e8;
}
.blog-preview:hover{
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  background-color: rgba(88, 52, 7, 0.2);
}
.blog-preview h2{
  font-size: 25px;
  color: #583407;
  margin-bottom: 5px;
}
.blog-preview a{
  text-decoration: none;
}
.blog-preview svg{
  position: relative;
  left: 98%;
  bottom: 20px;
}
.type{
  display: flex;
  flex-direction: row;
  position: relative;
  top: 8px;
}

/* blog details */
.loading{
  width: 15%;
  height: 15%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
article h1{
  color: #583407;
  font-size: x-large;
  font-weight: bolder;
  margin-bottom: 5px;
}
article h4{
  font-size: 14px;
  font-weight: bolder;
  margin-bottom: 10px;
}
.blog-details button{
  margin-top: 30px;
  padding: 8px;
  border-radius: 10px;
  border: none;
  font-weight: 600;
  background-color: black;
  color: white;
  cursor: pointer;
}
/* all error messages */
.error-message{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: x-large;
  width: auto;
}
/* craete blog */
.create{
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create h2{
  color: #583407;
  margin-bottom: 30px;
}
.create label{
  text-align: left;
  font-weight: bolder;
  display: block;
}
.create input, .create textarea, .create select{
  display: block;
  width: 100%;
  padding: 8px 10px;
  margin: 10px 0;
  border: 1px solid #ec9b95;
  box-sizing: border-box;
  border-radius: 5px;
  font-weight: 600;
}
select{
  background-color: #f0e1e0;
}
.create button{
  width: auto;
  background-color: #000000;
  color: white;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 1s ease-in-out;
}
.create button:hover{
  background-color: white;
  color: black;
  border: 1px solid #000000;
  padding: 8px;
  border-radius: 8px;
  transform: scale(1.15);
}
#blog-loading-btn{
  width: 50%;
  background-color:#f2f2f2;
  color: #000000;
  font-weight: bolder;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.loading-icon{
  width: 20%;
}


